import Vue from 'vue'
import Router from 'vue-router'

function loadView (view) {
  return () => import(/* webpackChunkName: "view-[request]" */ `../components/${view}.vue`)
}

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: loadView('Main'),
      children: [
        {
          path: '',
          name: 'introductions',
          component: loadView('introductions')
        },
        {
          path: 'skills',
          name: 'skills',
          component: loadView('skills')
        },
        {
          path: 'organizations',
          name: 'organizations',
          component: loadView('organizations')
        },
        {
          path: 'software',
          name: 'software',
          component: loadView('software')
        },
        {
          path: 'works',
          name: 'works',
          component: loadView('works')
        }
      ]
    }
  ]
})



